h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

.cat-info {
  gap: 15px;
  margin-left: 20px;
  margin-right: 20px;
  display: flex;
}

.loader {
  perspective: 1000px;
  width: 20px;
  height: 20px;
  color: #0f0;
  border-radius: 50%;
  margin-left: 20px;
  transform: rotateZ(45deg);
}

.loader:before, .loader:after {
  content: "";
  width: inherit;
  height: inherit;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  transform: rotateX(70deg);
}

.loader:after {
  color: #1c87eb;
  animation-delay: .4s;
  transform: rotateY(70deg);
}

.load {
  gap: 5px;
  margin: 0;
  display: flex;
}

.is-hidden {
  display: none;
}

